interface ButtonProps {
    onClick: () => void;
    disabled?: boolean;
    className?: string;
    children: React.ReactNode;
    primary?: boolean;
    label?: string;
    size?: 'small' | 'medium' | 'large';
    backgroundColor?
    style?: {};
}

import React from 'react';

const handleClick = () => {
    alert('Button clicked!');
};

 const Button: React.FC<ButtonProps> = ({ onClick, disabled, className, style,children }) => {
     return (
         <button
             style={{
                 color: 'white',
                 backgroundColor: 'skyblue',
                 height: 30,
                 width: 70,
                 borderRadius: 10
             }}
             name="ButtonName"
             onClick={handleClick}
             disabled={disabled}
             className={className}
         >
             {children}
             Click Me
         </button>
     );
 };

export default Button;